/* Month header */
.month {
  /* padding: 70px 25px; */
  width: 100%;
  background: #1abc9c;
  text-align: center;
}

/* Month list */
.month ul {
  margin: 0;
  padding: 0;
}

.month ul li {
  color: white;
  font-size: 20px;
  text-transform: uppercase;
  letter-spacing: 3px;
}

/* Previous button inside month header */
.month .prev {
  float: left;
  padding-top: 10px;
}

/* Next button */
.month .next {
  float: right;
  padding-top: 10px;
}

/* Weekdays (Mon-Sun) */
.weekdays {
  width:100%;
  margin: 0;
  padding: 10px 0;
  background-color:#ddd;
}

.weekdays thead tr td {
  width: 13.6%;
  color: #666;
  text-align: center;
}

/* Days (1-31) */
.days {
  padding: 10px 0;
  background: #eee;
  margin: 0;
}

.days tr td {
  list-style-type: none;
  width: 13.6%;
  text-align: center;
  margin-bottom: 5px;
  font-size:15px;
  color: #777;
  padding:10px;
  border: 2px solid #f9f8ff;
  cursor: pointer;
}

/* Highlight the "current" day */
.days tr td .active {
  padding: 5px;
  background: #1abc9c;
  color: white !important
}

